.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
/*
  這邊是為了解決vue-cli建置的項目在 <style lang="scss" scoped> 會有scope 的限制, 
  所以將 html 跟 body 這樣的樣式設定在這邊...這是治標的方法, 
  理論上最好是修改vue-cli的環境設定已達到統一用 scss-load 去處理... 但是這樣會有vue-cli改版之後環境要從設的問題

  這方法的缺點就是無法套用 _var_fn.scss 定義的變數, 如果要改顏色等等要自己手動改.
*/
html, body {
  background-color: #eceae8;
  font-size: 16px;
  /*使用思源黑體*/
  font-family: Arial, Helvetica, 'Noto Sans TC', 'LiHei Pro', "\5FAE\8EDF\6B63\9ED1\9AD4", "\65B0\7D30\660E\9AD4", 'Microsoft JhengHei', sans-serif;
}
/* 思源黑體
* Noto Sans TC (Chinese_traditional) http://www.google.com/fonts/earlyaccess
 */
@font-face {
  font-family: 'Noto Sans TC';
  font-style: normal;
  font-weight: 100;
  src: url(//fonts.gstatic.com/ea/notosanstc/v1/NotoSansTC-Thin.woff2) format('woff2'),
       url(//fonts.gstatic.com/ea/notosanstc/v1/NotoSansTC-Thin.woff) format('woff'),
       url(//fonts.gstatic.com/ea/notosanstc/v1/NotoSansTC-Thin.otf) format('opentype');
}
@font-face {
  font-family: 'Noto Sans TC';
  font-style: normal;
  font-weight: 300;
  src: url(//fonts.gstatic.com/ea/notosanstc/v1/NotoSansTC-Light.woff2) format('woff2'),
       url(//fonts.gstatic.com/ea/notosanstc/v1/NotoSansTC-Light.woff) format('woff'),
       url(//fonts.gstatic.com/ea/notosanstc/v1/NotoSansTC-Light.otf) format('opentype');
}
